button {
  background: none !important;
  border: none;
  padding: 0 !important;
  cursor: pointer;
}

.navbar-brand {
  margin-left: 10px;
  font-size: 2.5em;
  font-family: 'Times New Roman', cursive;
}

.nav-item {
  font-size: 1.5em;
}

.tab {
  /*optional*/
  font-family: arial, sans-serif;
  /*input has OS specific font-family*/
  color: #069;
  cursor: pointer;
}

li {
  display: block;
  padding: 0px 20px;
}

.active {
  border-bottom: 3px solid lightblue;
  padding-bottom: 5px;
}
